// Calendar Component
const CalendarWidget = () => {
  const [currentDate, setCurrentDate] = useState(new Date(2025, 7, 4)); // August 2025
  
  const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  const monthNames = ['January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December'];

  const getDaysInMonth = () => {
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth();
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    const daysInMonth = lastDay.getDate();
    const startingDay = firstDay.getDay();

    const days = [];
    
    // Add empty cells for days before the first day of the month
    for (let i = 0; i < startingDay; i++) {
      days.push(null);
    }
    
    // Add days of the month
    for (let day = 1; day <= daysInMonth; day++) {
      days.push(day);
    }
    
    return days;
  };

  const timeSlots = [
    { time: '12AM', period: 'night' },
    { time: '1AM', period: 'night' },
    { time: '2AM', period: 'night' },
    { time: '3AM', period: 'night' },
    { time: '4AM', period: 'night' },
    { time: '5AM', period: 'early' },
    { time: '6AM', period: 'morning' },
    { time: '7AM', period: 'morning' },
  ];

  return (
    <div className="bg-gray-900/20 backdrop-blur-md border border-gray-700/30 rounded-lg p-4 h-full">
      {/* Reminders Section */}
      <div className="mb-6">
        <div className="flex items-center justify-between mb-3">
          <h2 className="text-white font-medium">Reminders</h2>
          <div className="flex items-center gap-2">
            <span className="bg-blue-500 text-white text-xs px-2 py-1 rounded">Upcoming</span>
            <span className="text-gray-400 text-xs">Past</span>
            <MoreHorizontal className="w-4 h-4 text-gray-400" />
          </div>
        </div>
        <div className="bg-yellow-500/10 border border-yellow-500/30 rounded-lg p-3">
          <div className="flex items-start gap-2">
            <div className="w-2 h-2 bg-yellow-500 rounded-full mt-2"></div>
            <div>
              <p className="text-white text-sm">No upcoming reminders. Tell Martin to set a reminder.</p>
              <p className="text-gray-400 text-xs mt-1">He will send you a text message then.</p>
            </div>
          </div>
        </div>
        <button className="w-full mt-3 py-2 text-center bg-gray-800/50 hover:bg-gray-800/70 border border-gray-600/50 rounded-lg text-white text-sm">
          Reminder +
        </button>
      </div>

      {/* Calendar Section */}
      <div>
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-white font-medium">Calendar</h2>
          <div className="flex items-center gap-2">
            <div className="w-4 h-4 bg-blue-500 rounded"></div>
            <span className="text-gray-400 text-xs">1 Account Connected</span>
            <MoreHorizontal className="w-4 h-4 text-gray-400" />
          </div>
        </div>

        {/* Calendar Navigation */}
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-2">
            <ChevronLeft className="w-4 h-4 text-gray-400 hover:text-white cursor-pointer" />
            <ChevronRight className="w-4 h-4 text-gray-400 hover:text-white cursor-pointer" />
            <button className="text-white text-sm">Today</button>
          </div>
          <span className="text-white font-medium">{monthNames[currentDate.getMonth()]} {currentDate.getFullYear()}</span>
        </div>

        {/* Calendar Grid */}
        <div className="grid grid-cols-7 gap-1 mb-4">
          {daysOfWeek.map(day => (
            <div key={day} className="text-center text-gray-400 text-xs font-medium py-2">
              {day}
            </div>
          ))}
          {getDaysInMonth().map((day, index) => (
            <div key={index} className="text-center p-2 hover:bg-gray-800/50 rounded cursor-pointer">
              {day && (
                <span className={`text-sm ${day === 4 ? 'text-white bg-blue-500 w-6 h-6 rounded-full flex items-center justify-center mx-auto' : 'text-gray-300'}`}>
                  {day}
                </span>
              )}
            </div>
          ))}
        </div>

        {/* Time Slots */}
        <div className="space-y-2">
          {timeSlots.map((slot, index) => (
            <div key={index} className="flex items-center justify-between py-2 border-b border-gray-800/50">
              <span className="text-gray-400 text-xs">{slot.time}</span>
              <div className="flex-1 ml-4"></div>
            </div>
          ))}
        </div>

        <button className="w-full mt-4 p-3 bg-gray-800/50 hover:bg-gray-800/70 border border-gray-600/50 rounded-lg text-gray-400">
          <Plus className="w-5 h-5 mx-auto" />
        </button>
      </div>

      {/* Notes Section */}
      <div className="mt-6">
        <div className="flex items-center justify-between">
          <h3 className="text-white font-medium">Notes</h3>
          <MoreHorizontal className="w-4 h-4 text-gray-400" />
        </div>
      </div>
    </div>
  );
};

// AI Chat Assistant Component
const AIAssistant = () => {
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([
    {
      id: 1,
      type: 'ai',
      content: 'Could you clarify what you\'d like me to do regarding "call"? Are you asking me to make a phone call, schedule one, or assist with something related to calls? Let me know how I can help!',
      timestamp: 'Aug 4 at 10:14 PM'
    },
    {
      id: 2,
      type: 'user',
      content: 'Call Me',
      timestamp: 'Aug 4 at 10:14 PM'
    },
    {
      id: 3,
      type: 'ai',
      content: 'Currently, I don\'t have the ability to make direct phone calls. However, I can assist you in scheduling a call, sending a message, or providing instructions for a call. Let me know how you\'d like to proceed!',
      timestamp: 'Aug 4 at 10:14 PM'
    },
    {
      id: 4,
      type: 'user',
      content: 'Start Phone Call',
      timestamp: 'Aug 4 at 10:14 PM'
    },
    {
      id: 5,
      type: 'ai',
      content: 'Could you please provide the following details so I can proceed with the call?\n\n1. Name of the person you want me to call.\n2. Phone number of the person (if it\'s not in your contacts).\n3. Instructions for the call (what you want me to accomplish during the conversation).\n4. Any additional context that might help me during the call.\n\nLet me know, and I\'ll take care of it!',
      timestamp: 'Aug 4 at 10:14 PM'
    }
  ]);

  const callRequests = [
    {
      id: 1,
      contact: 'fadi null (+923127979378)',
      instructions: 'Ask Fadi if he will come tomorrow.',
      context: 'Ali wants Martin to call Fadi at the provided phone number and ask him if he will come tomorrow. No additional context has been provided.',
      timestamp: 'Aug 4 at 10:16 PM'
    }
  ];

  const handleSendMessage = () => {
    if (message.trim()) {
      setMessages([...messages, {
        id: messages.length + 1,
        type: 'user',
        content: message,
        timestamp: new Date().toLocaleString()
      }]);
      setMessage('');
    }
  };

  return (
    <div className="bg-gray-900/20 backdrop-blur-md border border-gray-700/30 rounded-lg h-full flex flex-col">
      {/* Header */}
      <div className="flex items-center justify-between p-4 border-b border-gray-700/30">
        <div className="flex items-center gap-2">
          <ChevronLeft className="w-5 h-5 text-gray-400" />
          <h2 className="text-white font-medium">Assistance with Call Requests</h2>
        </div>
        <div className="flex items-center gap-2">
          <MoreHorizontal className="w-5 h-5 text-gray-400" />
        </div>
      </div>

      {/* Chat Messages */}
      <div className="flex-1 p-4 space-y-4 overflow-y-auto max-h-96">
        {messages.map((msg) => (
          <div key={msg.id} className={`flex ${msg.type === 'user' ? 'justify-end' : 'justify-start'}`}>
            <div className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
              msg.type === 'user' 
                ? 'bg-blue-600 text-white' 
                : 'bg-gray-800/50 text-gray-200'
            }`}>
              <p className="text-sm">{msg.content}</p>
              <span className="text-xs opacity-70 mt-1 block">{msg.timestamp}</span>
            </div>
          </div>
        ))}

        {/* Call Request Cards */}
        {callRequests.map((request) => (
          <div key={request.id} className="bg-gray-800/30 border border-gray-700/50 rounded-lg p-4 mb-4">
            <div className="flex items-center justify-between mb-2">
              <span className="text-white font-medium text-sm">Call</span>
              <span className="text-gray-400 text-xs">yes</span>
            </div>
            <p className="text-white text-sm mb-2">{request.contact}</p>
            <div className="mb-3">
              <p className="text-gray-300 text-xs mb-1"><strong>Instructions:</strong></p>
              <p className="text-gray-300 text-xs mb-2">{request.instructions}</p>
              <p className="text-gray-300 text-xs mb-1"><strong>Context:</strong></p>
              <p className="text-gray-300 text-xs">{request.context}</p>
            </div>
            <div className="flex gap-2 mb-3">
              <button className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded text-sm">
                Call
              </button>
              <button className="bg-gray-600 hover:bg-gray-700 text-white px-4 py-2 rounded text-sm">
                Cancel
              </button>
            </div>
            <p className="text-gray-400 text-xs">Should I proceed with the call? Let me know!</p>
            <span className="text-gray-400 text-xs">{request.timestamp}</span>
          </div>
        ))}
      </div>

      {/* Input Area */}
      <div className="p-4 border-t border-gray-700/30">
        <div className="flex items-center gap-2 mb-2">
          <span className="text-sm text-gray-400">GPT-4o</span>
          <Phone className="w-4 h-4 text-gray-400" />
          <MessageSquare className="w-4 h-4 text-gray-400" />
        </div>
        <div className="flex items-center gap-2">
          <input
            type="text"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
            placeholder="What do you want to do?"
            className="flex-1 bg-gray-800/50 border border-gray-600/50 rounded-lg px-4 py-2 text-white placeholder-gray-400 focus:outline-none focus:border-gray-500"
          />
          <button
            onClick={handleSendMessage}
            className="bg-green-600 hover:bg-green-700 p-2 rounded-lg"
          >
            <Send className="w-4 h-4 text-white" />
          </button>
        </div>
      </div>
    </div>
  );
};

